@import "_import";

.error {
  overflow: hidden;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
  min-height: 768rem;
  background-color: #fff;
  @include full {
    min-height: 768px;
  }
  @include mobile {
    height: auto;
    min-height: calc(100vh - 280rem);
    padding: 265rem 0 253rem;
  }
  &__txt {
    width: 100%;
    margin: -80rem 0 0 0;
    text-align: center;
    @include full {
      margin: -80px 0 0 0;
    }
    @include mobile {
      margin: 0;
    }
  }
  &__desc {
    margin: 0 0 48rem 0;
    font-family: "Noto Sans KR Medium";
    font-size: 28rem;
    line-height: 1.6;
    letter-spacing: -0.3rem;
    @include full {
      margin: 0 0 48px 0;
      font-size: 28px;
      letter-spacing: -0.3px;
    }
    @include mobile {
      margin: 0 0 34rem 0;
      font-size: 18rem;
      line-height: 1.5;
    }
  }
  &__btn {
    display: inline-block;
    width: 120rem;
    height: 49rem;
    line-height: 47rem;
    border: 2px solid #000;
    border-radius: 24.5px;
    font-family: "ambit-semi";
    font-size: 30rem;
    @include full {
      width: 120px;
      height: 49px;
      line-height: 47px;
      font-size: 30px;
    }
    @include mobile {
      width: 68rem;
      height: 36rem;
      line-height: 32rem;
      font-size: 16rem;
    }
    @include ie-only {
      line-height: 54rem;
      @include full {
        line-height: 54px;
      }
      @include mobile {
        line-height: 36rem;
      }
    }
    &--home {
      margin: 0 8rem 0 0;
      @include full {
        margin: 0 8px 0 0;
      }
    }
  }
  &__img-wrap {
    position: absolute;
    right: 0;
    bottom: 135rem;
    width: 377rem;
    @include full {
      right: 0;
      bottom: 135px;
      width: 377px;
    }
    @include mobile {
      right: -19rem;
      bottom: 76rem;
      width: 151rem;
    }
  }
  &__tit {
    display: flex;
    justify-content: center;
    position: absolute;
    bottom: -15rem;
    left: 3px;
    width: 100%;
    margin-left: 9rem;
    font-size: 200rem;
    line-height: 1.2;
    @include full {
      bottom: -15px;
      left: 0;
      margin-left: 9px;
      font-size: 200px;
      line-height: 1.21;
    }
    @include mobile {
      bottom: 9rem;
      left: 0;
      margin-left: 0;
      font-size: 42rem;
    }
    @include ie-only {
      bottom: -45rem;
      @include full {
        bottom: -45px;
      }
      @include mobile {
        bottom: -8rem;
      }
    }
  }
  &__tit-sep {
    &--1 {
      padding-right: 50rem;
      font-family: "roslindaletext";
      letter-spacing: -5.3rem;
      @include full {
        padding-right: 50px;
        letter-spacing: -5.3px;
      }
      @include mobile {
        padding-right: 12rem;
        letter-spacing: -1.3rem;
      }
    }
    &--2 {
      font-family: "ambit-semi";
      letter-spacing: -3.3rem;
      @include full {
        letter-spacing: -3.3px;
      }
      @include mobile {
        letter-spacing: -1.3rem;
      }
      @include ie-only {
        padding-top: 34rem;
        @include full {
          padding-top: 34px;
        }
        @include mobile {
          padding-top: 7px;
        }
      }
    }
  }
}

.c-footer {
  display: none;
  @include mobile {
    display: block;
  }
}